import React, { useRef, useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { FaGithub, FaLinkedin, FaTwitter, FaPaperPlane } from 'react-icons/fa';
import emailjs from '@emailjs/browser';

const Contact = () => {
  const formRef = useRef();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState(null);

  useEffect(() => {
    // Initialize EmailJS with your public key
    emailjs.init("YOUR_PUBLIC_KEY");
  }, []);

  const socialLinks = [
    { icon: <FaGithub size={24} />, url: 'https://github.com', label: 'GitHub' },
    { icon: <FaLinkedin size={24} />, url: 'https://linkedin.com', label: 'LinkedIn' },
    { icon: <FaTwitter size={24} />, url: 'https://twitter.com', label: 'Twitter' },
  ];

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      const result = await emailjs.sendForm(
        'YOUR_SERVICE_ID', // Replace with your EmailJS service ID
        'YOUR_TEMPLATE_ID', // Replace with your EmailJS template ID
        formRef.current
      );
      
      if (result.text === 'OK') {
        setSubmitStatus('success');
        formRef.current.reset();
      } else {
        throw new Error('Failed to send message');
      }
    } catch (error) {
      console.error('Email error:', error);
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
      setTimeout(() => setSubmitStatus(null), 5000);
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="w-full max-w-4xl mx-auto px-4 py-12"
    >
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
        {/* Contact Info */}
        <motion.div
          initial={{ x: -50 }}
          animate={{ x: 0 }}
          transition={{ duration: 0.5 }}
        >
          <h2 className="text-4xl font-bold mb-6">Get in Touch</h2>
          <p className="text-gray-400 mb-8">
            Have a project in mind? Let's talk about it. Send me a message and I'll get back to you as soon as possible.
          </p>
          
          <div className="flex gap-4 mb-8">
            {socialLinks.map((link, index) => (
              <motion.a
                key={index}
                href={link.url}
                target="_blank"
                rel="noopener noreferrer"
                className="p-3 bg-darker rounded-lg text-gray-400 hover:text-primary hover:shadow-glow transition-all"
                whileHover={{ scale: 1.1, rotate: [0, -10, 10, 0] }}
                whileTap={{ scale: 0.9 }}
              >
                {link.icon}
              </motion.a>
            ))}
          </div>
        </motion.div>

        {/* Contact Form */}
        <motion.form
          ref={formRef}
          onSubmit={handleSubmit}
          initial={{ x: 50 }}
          animate={{ x: 0 }}
          transition={{ duration: 0.5 }}
          className="space-y-6"
        >
          <div>
            <motion.input
              type="text"
              name="user_name"
              required
              placeholder="Your Name"
              className="w-full p-4 bg-darker rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-primary transition-shadow"
              whileFocus={{ scale: 1.02 }}
            />
          </div>
          
          <div>
            <motion.input
              type="email"
              name="user_email"
              required
              placeholder="Your Email"
              className="w-full p-4 bg-darker rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-primary transition-shadow"
              whileFocus={{ scale: 1.02 }}
            />
          </div>
          
          <div>
            <motion.textarea
              name="message"
              required
              placeholder="Your Message"
              rows="6"
              className="w-full p-4 bg-darker rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-primary transition-shadow resize-none"
              whileFocus={{ scale: 1.02 }}
            />
          </div>

          <motion.button
            type="submit"
            disabled={isSubmitting}
            className="w-full bg-primary text-white py-4 rounded-lg flex items-center justify-center gap-2 hover:bg-primary-light disabled:opacity-50"
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
          >
            <FaPaperPlane className={isSubmitting ? 'animate-bounce' : ''} />
            {isSubmitting ? 'Sending...' : 'Send Message'}
          </motion.button>

          {submitStatus && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              className={`text-center p-3 rounded ${
                submitStatus === 'success' 
                  ? 'bg-green-500/20 text-green-500' 
                  : 'bg-red-500/20 text-red-500'
              }`}
            >
              {submitStatus === 'success' 
                ? 'Message sent successfully!' 
                : 'Failed to send message. Please try again.'}
            </motion.div>
          )}
        </motion.form>
      </div>
    </motion.div>
  );
};

export default Contact;
